<template>
  <div :class="{ dropup: !dropicon }">
    <b-button
      class="butr dropdown-toggle"
      v-b-toggle="id"
      variant="secondary"
      @click="dropicon = !dropicon"
      >{{ header }}</b-button
    >
    <b-collapse :id="id" class="mt-2">
      <b-card>
        <p class="card-text">{{ content }}</p>
      </b-card>
       <button class="btn-copy" @click="copy(id)">
      copy
    </button>
    </b-collapse>
   
  </div>
</template>
<script>
export default {
  props: ["header", "content", "id"],
  data() {
    return {
      dropicon: true,
    };
  },
  methods: {
    copy: function(id) {
      var from = document.getElementById(id).firstElementChild;
      var range = document.createRange();
      window.getSelection().removeAllRanges();
      range.selectNode(from);
      window.getSelection().addRange(range);
      document.execCommand("copy");
      window.getSelection().removeAllRanges();
    },
  },
};
</script>
<style>
.butr {
  margin-bottom: 0.2em;
}
.card-body {
  width: 90%;
  margin-left: 5%;
}
.collapse {
  text-align-last: center;
}
.pills-tab {
  background-color: blackness(0%);
}
.btn-secondary {
  width: 100%;
  text-align: left !important;

  font-size: 1em;
  white-space: normal;
}
.nav-link {
  border: solid 1px #0d6efd !important;
  margin: 0.1rem;
}
.nav-link:hover {
  background-color: #c0ebc6 !important;
  color: #e95858 !important;
  border: solid 1px #badbcc !important;
}
</style>
